@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  background-color: hsl(0, 0%, 98%);
}

svg {
  display: flex;
  align-items: center;
}

.text-shadow-custom-large {
  text-shadow: 0px 0px 40px rgba(0, 0, 0, 0.5);
}

.text-shadow-custom-small {
  text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
}
